<template>
  <div>
    <el-form ref="form" :model="form" label-width="100px">
      <el-tabs v-model="activeName">
        <el-tab-pane label="" name="first">
          <div class="scroll_content">
            <el-form-item label="" style="width:500px;">
              <el-input v-model="form.title" clearable placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" style="width:500px;">
              <el-select v-model="form.industry" clearable>
                <el-option label="jquery" value="8"></el-option>
                <el-option label="html" value="9"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-input-number v-model="form.price" controls-position="right" :min="0"></el-input-number>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="form.isOriginal">
                <el-radio label="0"></el-radio>
                <el-radio label="1"></el-radio>
              </el-radio-group>
            </el-form-item>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <vue-ueditor-wrap
            ref="ueditor"
            v-model="form.content"
            :destroy="false"
            :config="editorConfig"
            @ready="ready"
          ></vue-ueditor-wrap>
        </el-tab-pane>
        <el-tab-pane label="" name="third">
          <div class="scroll_content">
            <el-scrollbar class="page-component__scroll">
              <el-upload
                class="upload-demo"
                ref="upload"
                action="https://jsonplaceholder.typicode.com/posts/"
                :file-list="form.fileList"
                :auto-upload="false"
                list-type="picture"
                :limit="1"
                :multiple="false"
              >
                <el-button slot="trigger" size="small" type="primary"></el-button>
                <el-button style="margin-left: 10px;" size="small" type="success"></el-button>
                <div slot="tip" class="el-upload__tip">jpg,jpeg,png,gif100KB</div>
              </el-upload>
            </el-scrollbar>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="forth">
          <el-form-item label="">
            <el-checkbox-group v-model="form.download_type" @change="checkBox">
              <el-checkbox label="1"></el-checkbox>
              <el-checkbox label="2"></el-checkbox>
              <el-checkbox label="3">360</el-checkbox>
              <el-checkbox label="4">115</el-checkbox>
              <el-checkbox label="5"></el-checkbox>
              <el-checkbox label="6"></el-checkbox>
              <el-checkbox label="8"></el-checkbox>
              <el-checkbox label="99"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <div class="download_content">
            <el-scrollbar class="page-component__scroll">
              <!--  -->
              <el-form-item>
                <el-upload
                  class="upload-demo"
                  ref="upload"
                  action="https://jsonplaceholder.typicode.com/posts/"
                  :file-list="form.localFile"
                  :auto-upload="false"
                  :limit="1"
                  :multiple="false"
                  v-show="downloadFileAttr.hasLocalFile"
                >
                  <el-button slot="trigger" size="small" type="primary"></el-button>
                  <el-button style="margin-left: 10px;" size="small" type="success"></el-button>
                  <div slot="tip" class="el-upload__tip">zip2MB</div>
                </el-upload>
              </el-form-item>

              <!--  -->
              <el-form-item label="" style="width:500px;" v-show="downloadFileAttr.baiduPan">
                <el-input v-model="form.download2_url" clearable placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" style="width:500px;" v-show="downloadFileAttr.baiduPan">
                <el-input v-model="form.download2_pwd" clearable placeholder=""></el-input>
              </el-form-item>

              <!-- 360 -->
              <el-form-item label="360" style="width:500px;" v-show="downloadFileAttr.Pan360">
                <el-input v-model="form.download2_url" clearable placeholder="360"></el-input>
              </el-form-item>
              <el-form-item label="360" style="width:500px;" v-show="downloadFileAttr.Pan360">
                <el-input v-model="form.download2_pwd" clearable placeholder="360"></el-input>
              </el-form-item>

              <!-- 115 -->
              <el-form-item label="115" style="width:500px;" v-show="downloadFileAttr.Pan115">
                <el-input v-model="form.download2_url" clearable placeholder="115"></el-input>
              </el-form-item>
              <el-form-item label="115" style="width:500px;" v-show="downloadFileAttr.Pan115">
                <el-input v-model="form.download2_pwd" clearable placeholder="115"></el-input>
              </el-form-item>
            </el-scrollbar>
          </div>
        </el-tab-pane>
      </el-tabs>
    </el-form>
  </div>
</template>

<script>
import VueUeditorWrap from "vue-ueditor-wrap"; // ES6 Module
export default {
  name: "template_form",
  components: {
    VueUeditorWrap
  },
  props: {
    artId: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      activeName: "first",
      form: {
        title: "",
        industry: "",
        type: "",
        language: "",
        format: [],
        color: "",
        price: 0,
        isOriginal: "0",
        content: "",
        fileList: [],
        download_type: [],
        localFile: [],
        download2_url: "",
        download2_pwd: ""
      },
      editorConfig: {
        // 
        autoHeightEnabled: false,
        // 
        initialFrameHeight: 440,
        // 
        initialFrameWidth: "500px",
        // 
        // serverUrl: "http://admin.azz.lc/Public/plugin/ueditor/php/controller.php",
        // UEditor  vue-cli vue-ueditor-wrap 2
        // UEDITOR_HOME_URL: "/static/UEditor/",
        // Request Headers,https://github.com/HaoChuan9421/ueditor/commits/dev-1.4.3.3
        // headers: {
        //   access_token: '37e7c9e3fda54cca94b8c88a4b5ddbf3'
        // },
        toolbars: [
          [
            "Undo",
            "Redo",
            "Bold",
            "italic",
            "underline",
            "forecolor",
            "backcolor",
            "insertorderedlist",
            "insertunorderedlist",
            "justifyleft",
            "justifycenter",
            "justifyright",
            "justifyjustify",
            "emotion",
            "simpleupload",
            "insertcode",
            "autotypeset",
            "inserttable",
            "spechars",
            "link",
            "unlink"
          ]
        ]
      },
      downloadFileAttr: {
        hasLocalFile: false,
        baiduPan: false,
        Pan360: false,
        Pan115: false
      }
    };
  },
  methods: {
    ready(editorInstance) {
      console.log(`${editorInstance.key}:`, editorInstance);
      console.log(111);
    },
    checkBox(value) {
      // 
      if (value.indexOf("1") >= 0) {
        this.downloadFileAttr.hasLocalFile = true;
      } else {
        this.downloadFileAttr.hasLocalFile = false;
      }

      // 
      if (value.indexOf("2") >= 0) {
        this.downloadFileAttr.baiduPan = true;
      } else {
        this.downloadFileAttr.baiduPan = false;
      }

      // 360
      if (value.indexOf("3") >= 0) {
        this.downloadFileAttr.Pan360 = true;
      } else {
        this.downloadFileAttr.Pan360 = false;
      }

      // 115
      if (value.indexOf("4") >= 0) {
        this.downloadFileAttr.Pan115 = true;
      } else {
        this.downloadFileAttr.Pan115 = false;
      }
    }
  },
  watch: {
    artId(newValue, oldValue) {
      // 
      console.log('' + newValue);
      console.log('' + oldValue);
    }
  }
};
</script>

<style scoped>
.download_content {
  height: 400px;
  overflow: hidden;
}
</style>